.original-button {
  display: inline-block;
  font-family: "Open Sans", sans-serif;
  text-decoration: none;
  color: #FFFFFF;
  font-weight: 500;
  font-size: 18px;
  line-height: 50px;
  padding: 0 35px;
  border-radius: 50px;
  cursor: pointer;
  background: #d04751;
  /* Old browsers */
  background: -moz-linear-gradient(left, #d04751 1%, #f1b933 77%);
  /* FF3.6-15 */
  background: -webkit-linear-gradient(left, #d04751 1%, #f1b933 77%);
  /* Chrome10-25,Safari5.1-6 */
  background: linear-gradient(to right, #d04751 1%, #f1b933 77%);
  /* W3C, IE10+, FF16+, Chrome26+, Opera12+, Safari7+ */
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#d04751', endColorstr='#f1b933',GradientType=1 );
  /* IE6-9 */ }
  .original-button:hover {
    text-decoration: underline; }
  .original-button-holder {
    padding: 30px 0;
    text-align: center; }

.cols {
  position: relative;
  text-align: center;
  font-size: 0; }

.col {
  font-size: 16px;
